<template>
  <div id="login">
        <div class="loginPage">
          <div class="logo"></div>
          <div class="loginCon">
            <div class="loginBtn">
              <p class="user putNow">
                <span class="iconfont icon-zhanghao"></span>
                <span>账号 :</span>
                <input type="text"  v-model="user"/>
              </p>
              <p class="pass putNow">
                <span class="iconfont icon-mima"></span>
                <span>密码 :</span>
                <input type="password" v-model="pass" />
              </p>
              <!-- <a href="#">注册/忘记密码?</a> -->
              <router-link to="/register" class="register">注册</router-link>
              <router-link to="/changePass" class="changePass">忘记密码?</router-link>
              <mt-button type="primary" size="large" @click="login">登录</mt-button>
            </div>
            <div class="thirdPlatform">
              <p>----------第三方登录平台-----------</p>
              <ul>
                <li v-for="(item,index) in platformList" :key="index">
                 <a href="#" @click="qrcodes(item)">
                    <span :class="['iconfont',item.iconClass]"></span>
                  </a> 
                </li>
              </ul>
            </div>
          </div>
        </div>

        <div class="mask" v-show="isShow" @click="clerMask">
          <div class="wrap" ref="msk" @click.stop="mskNow">
              <div id="qrcode" ref="qrCodeUrl"></div>  
          </div>
        </div>
  </div>
</template>

<script>
import QRCode from 'qrcodejs2'
import { Toast } from 'mint-ui';
export default {
  name: "Login",
  data() {
    return {
      num: 3,
      user:"",
      pass:"",
      account:[],
      isShow:false,
      platformList:[
        {iconClass:'icon-QQ',img:'../assets/QQ.png'},
        {iconClass:'icon-weibo',img:'../assets/weibo.png'},
        {iconClass:'icon-weixin',img:'../assets/weixin.png'}
        ]
    };
  },
  methods:{
     findElem(array, attr, val) {
    for (var i = 0; i < array.length; i++) {
        if (array[i][attr] == val) {
            return i; //返回当前索引值
        }
    }
    return -1;
},
  qrcodes(obj){
    this.isShow = true
    this.$refs.qrCodeUrl.innerHTML = ''
    var qrcode = new QRCode(this.$refs.qrCodeUrl, {
      text: 'http://localhost:8080/home',
      width: 200,
      height: 200,
      colorDark: '#000000',
      colorLight: '#ffffff',
      correctLevel: QRCode.CorrectLevel.H
 })
   
  console.log(qrcode)
  },
      login(){
          if(this.user == ""){
              Toast('账号不能为空');
          }else if(this.pass == ""){
              Toast('密码不能为空');
          }else{
        let userList = localStorage.getItem("account");      
        if(userList){
            userList = JSON.parse(userList)
            console.log(userList)
            let index = this.findElem(userList,'username',this.user)
            if(index == -1){
              Toast("没有该用户,可前注册页面注册")
            }else{
              if(userList[index].password !== this.pass){
                Toast("该账户&密码匹配不对")
              }else{
                 Toast("登录成功")
                 sessionStorage.setItem('useName',this.user)
                 setTimeout(() => {
                   this.$router.push('/home')
                 }, 2000);
              }
              
            }
        }

          }
      },

      clerMask(){
        this.isShow = false
      },

      mskNow(){
        this.isShow = true
      }

  }
};
</script>
<style scoped>
#login {
  width: 100%;
  height: 100%;
  background: url("../assets/login3.jpg") no-repeat 50% 50%;
  background-size: cover;
}

.loginCon {
  width: 6rem;
  position: absolute;
  left: 50%;
  top: 30%;
  transform: translateX(-50%);
}
.loginBtn {
  font-size: 15px;
  color: #000;
}
.loginBtn .iconfont {
  margin-right: 5px;
  margin-top: 2px;
}
.loginBtn span{
    color: #c8c8cd;
}
.loginBtn .putNow{
  width: 5rem;
  height: 0.8rem;
  border-radius: 10px;
  background: #005da0;
  margin: 0 auto;
  text-align: center;
  box-shadow: 1px 1px 1px 1px;
  margin-top: 10px;
}
.user input,
.pass input {
  width: 3.2rem;
  height: 0.8rem;
  border-radius: 10px;
  border: none;
  background: #005da0;
  outline: none;
  padding-left: 10px;
  color: #c8c8cd;
}
.loginBtn .pass{
  margin-bottom: 5px;
}
.loginBtn a {
   color: #c8c8cd;
}
.loginBtn .register{
  margin-left: 0.72rem;
}
.loginBtn .changePass{
  margin-left: 2.64rem;
}
.loginBtn .mint-button {
  margin-top: 0.4rem;
}
.thirdPlatform {
  font-size: 0.3rem;
  color: #fdfdfd;
  margin-top: 75px;
  text-align: center;
}
.thirdPlatform ul{
    display: flex;
    justify-content: center;
    margin-top: 30px;
}
.thirdPlatform ul li{
    width: 33%;
}
.thirdPlatform .icon-QQ,.icon-weibo,.icon-weixin{
    font-size: 0.5rem;
    color: #c8c8cd;
}
.mask{
  width: 100%;
  height: 100%;
  background: crimson;
  background: rgb(51 51 51 / 0.8);
}
.wrap{
 width: 6rem;
    height: 6.6rem;
    border-radius: 10px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    background: rgb(250 250 250);
    box-shadow: 0px 7px 12px 10px #293c49;
}
.wrap{
  display: flex;
  justify-content: center;
  align-items: center;
}
#qrcode{
  box-shadow: 0px 1px 20px 7px #888;
}
</style>