import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '@/views/Login'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Login',
    component: Login
  },
  {
    path: '/home',
    name: 'Home',
    component: () => import('../views/Home'),
    children:[
      {
        path:'/homeconten',
        name:'HomeConten',
        title:'首页',
        component:() => import( '../components/home/contents'),
      },
      {
        path: '/home',
       redirect: { name: 'HomeConten' }
      },
      {
        path:'/weather',
        name:'Weather',
        title:'天气',
        component:() => import( '../components/weather/weather'),
      },
      {
        path:'/book',
        name:'Book',
        title:'图书',
        component:() => import('../components/books/book'),
      },
      {
        path:'/mine',
        name:'Mine',
        title:'我的',
        component:() => import( '../components/mine/mine'),
      },
      {
        path: '/carepair',
        name: 'Carepair',
        component: () => import('../views/Carepair')
      },
      {
        path: '/conste',
        name: 'Conste',
        component: () => import('../views/Conste')
      },
      {
        path: '/oilprice',
        name: 'Oilprice',
        component: () => import(/* webpackChunkName: "about" */ '../views/Oilprice')
      },
      {
        path: '/lottery',
        name: 'Lottery',
        component: () => import(/* webpackChunkName: "about" */ '../views/Lottery')
      },
      {
        path: '/phone',
        name: 'Phone',
        component: () => import(/* webpackChunkName: "about" */ '../views/Phone')
      },
      {
        path: '/postcode',
        name: 'Postcode',
        component: () => import(/* webpackChunkName: "about" */ '../views/Postcode')
      },
    ]
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import(/* webpackChunkName: "about" */ '../views/Login')
  },
  {
    path: '/changePass',
    name: 'changePass',
    component: () => import(/* webpackChunkName: "about" */ '../components/accountment/changePass')
  },
  {
    path: '/register',
    name: 'register',
    component: () => import(/* webpackChunkName: "about" */ '../components/accountment/register')
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

// 解决 Vue 重复点击相同路由报错问题
const VueRouterPush =VueRouter.prototype.push
VueRouter.prototype.push = function push (to) {
  return VueRouterPush.call(this, to).catch(err => err)
}

router.beforeEach((to, from, next) => {
  let name = sessionStorage.getItem('useName')
  if(name){
    next()
  }else{
    if(to.name == 'Login'){
      next()
    }else{
      next({name:'Login'})
    }
  }
})

export default router
